    // Old version Start

    function solve_problem() {
        // <!-- This functions solves one blank in the SuDoKu. -->
        // <!-- Following are the steps -->
        // <!-- 1. Initialize the Row, Column and Square counts -->
        // <!-- 2. Get existing row, column and square counts -->
        // <!-- 3. Find Maximum count among the row/column/square counts -->
        // <!-- 4. Target the first empty cell in the maximum obtaioned from the previous step -->
        // <!-- 5. Loop 1 though 9 as possible values for the empty cell -->
        // <!-- 6. Check whether the value under test meets the row/column/square criteria -->
        // <!--	e.g. if the value exists in the same row, skip the value and go to next -->
        // <!-- 7. Check whether this value is to be avoided -->
        // <!--	e.g. if you have tried this value previously and you got stuck later on. Hence this value is marked as not be used -->
        // <!-- 8. If both conditions 6 and 7 are met, you got a value that can be kept in that cell -->
        // <!-- 9. Set the Value and exit this function -->
        // <!-- 10. If either of the conditions 6 or 7 fails, skip the value and go trough the loop -->
        // <!-- 11. If tried all 1 through 9 values and no value is suitable, somewhere in the past some of the value is incorrectly placed. Remove the previous value and mark that value as "to be avoided for that cell" and exit out of this function -->
        // <!-- Initialize row, column and square counts -->

        var vall;
        max_row_val = 0;
        max_row = 0;
        max_col_val = 0;
        max_col = 0;
        max_sq_val = 0;
        max_sq = 0;
        max_rcs_val = 0;
        max_rcs = 0;

        var i = 0;
        do {
            row_count[i] = 0;
            col_count[i] = 0;
            sq_count[i] = 0;
            i = i + 1;
        } while (i < 10);

        // <!-- Get initial row, column and square counts -->
        var c = 1;
        do {
            var r = 1;
            do {
                var s = get_sq_no(r, c);
                if (pos_val(r, c) != "") {
                    row_count[r] = row_count[r] + 1;
                    col_count[c] = col_count[c] + 1;
                    sq_count[s] = sq_count[s] + 1;
                }
                r = r + 1;
            } while (r < 10);
            c = c + 1;
        } while (c < 10);

        // <!-- Find maximum row, column and square counts -->
        r = 0;
        do {
            if ((row_count[r] != 9) && (row_count[r] > max_row_val)) {
                max_row_val = row_count[r];
                max_row = r;
            }
            if ((col_count[r] != 9) && (col_count[r] > max_col_val)) {
                max_col_val = col_count[r];
                max_col = r;
            }
            if ((sq_count[r] != 9) && (sq_count[r] > max_sq_val)) {
                max_sq_val = sq_count[r];
                max_sq = r;
            }
            r = r + 1;
        } while (r < 9);

        // <!-- Find maximum among row, column and square counts -->
        // <!-- max_rcs will be 1, 2 or 3 for row, column or square respectively -->
        // <!-- max_rcs_val will store the maximum count for the respective row, column or square -->
        max_rcs_val = 0;
        max_rcs = 0;
        if (max_row_val > max_rcs_val) {
            max_rcs_val = max_row_val;
            max_rcs = 1;
        }
        if (max_col_val > max_rcs_val) {
            max_rcs_val = max_col_val;
            max_rcs = 2;
        }
        if (max_sq_val > max_rcs_val) {
            max_rcs_val = max_sq_val;
            max_rcs = 3;
        }
        if (max_rcs_val == 0) {
            max_rcs_val = 0;
            max_rcs = 0;
            problem_solved = 1;
        }

        // <!-- If RCS = Row, complete row -->
        if (max_rcs == 1) {
            c = 0;
            do {
                if (pos_val(max_row, c) == "") {
                    vall = 1;
                    do {
                        if ((check_avoid(vall) == "1") && (check_rc(max_row, c, vall) == "")) {
                            itr = itr + 1;
                            itr_row[itr] = max_row;
                            itr_col[itr] = c;
                            itr_val[itr] = vall;
                            set_val(max_row, c, vall);
                            dump_val = dump_val + 1;
                            return ("");
                        }
                        if (vall == 9) {
                            k = clear_prev_itr();
                            return ("");
                        }
                        vall = vall + 1;
                    } while (vall < 10);
                }
                c = c + 1;
            } while (c < 10);
        }

        // <!-- If RCS = Column, complete column -->
        if (max_rcs == 2) {
            r = 0;
            do {
                if (pos_val(r, max_col) == "") {
                    vall = 1;
                    do {
                        if ((check_avoid(vall) == "1") && (check_rc(r, max_col, vall) == "")) {
                            itr = itr + 1;
                            itr_row[itr] = r;
                            itr_col[itr] = max_col;
                            itr_val[itr] = vall;
                            set_val(r, max_col, vall);
                            dump_val = dump_val + 1;
                            return ("");
                        }
                        if (vall == 9) {
                            var k = clear_prev_itr();
                            return ("");
                        }
                        vall = vall + 1;
                    } while (vall < 10);
                }
                r = r + 1;
            } while (r < 10);
        }

        // <!-- If RCS = Square, complete square -->
        if (max_rcs == 3) {
            var rel_pos = 1;
            do {
                vall = get_sq_rc(max_sq, rel_pos);
                if (pos_val(act_rr, act_cc) == "") {
                    vall = 1;
                    do {
                        if ((check_avoid(vall) == "1") && (check_rc(act_rr, act_cc, vall) == "")) {
                            itr = itr + 1;
                            itr_row[itr] = act_rr;
                            itr_col[itr] = act_cc;
                            itr_val[itr] = vall;
                            set_val(act_rr, act_cc, vall);
                            dump_val = dump_val + 1;
                            return ("");
                        };
                        if (vall == 9) {
                            k = clear_prev_itr();
                            return ("");
                        }
                        vall = vall + 1;
                    } while (vall < 10);
                }
                rel_pos = rel_pos + 1;
            } while (rel_pos < 10);
        }
        return ("");
    }


    function clear_prev_itr() {
        // <!-- This function is called due to condition 11 of solve_problem function -->
        // <!-- This functions clears the value of the last filled cell on the screen -->
        // <!-- It stores that this cell value for this cell is to be avoided -->
        // <!-- It removes all such avoids for cells which will be used in future -->
        set_val(itr_row[itr], itr_col[itr], "")
        if (isNaN(itr_avoid[itr]) == true) {
            itr_avoid[itr] = 0;
        }
        if (itr_avoid[itr] == "") {
            itr_avoid[itr] = 0;
        }
        itr_avoid[itr] = (itr_avoid[itr] * 10) + itr_val[itr];
        var i = 1;
        do {
            itr_avoid[itr + i] = 0;
            i = i + 1;
        } while (i < (85 - itr))
        itr_row[itr] = "";
        itr_col[itr] = "";
        itr_val[itr] = "";
        last_itr_cleared = itr;
        itr = itr - 1;
    }

    function check_avoid(test_val) {
        // <!-- This function is called due to condition 7 of solve_problem function -->
        // <!-- This functions checks whether the calue under consideration is to be avoided for that particular cell  -->
        if (isNaN(itr_avoid[itr + 1]) == true) {
            return ("1");
        }
        var itr_avoid_str = String(itr_avoid[itr + 1]);
        var test_val_str = String(test_val);
        if (itr_avoid_str.search(test_val_str) == -1) {
            return ("1");
        }
        return ("0");
    }

    function get_sq_rc(sq_no, rel_pos) {
        // <!-- This is a propritory function which is part of the IP!! Contact me for more details about this :) -->
        var rr = 0;
        var cc = 0;
        var rel_rr = 0;
        var rel_cc = 0;
        act_rr = 0;
        act_cc = 0;
        rr = Math.ceil(sq_no / 3) - 1;
        cc = sq_no - (rr * 3) - 1;
        rel_rr = Math.ceil(rel_pos / 3) - 1;
        rel_cc = rel_pos - (rel_rr * 3) - 1;
        act_rr = (rr * 3) + rel_rr + 1;
        act_cc = (cc * 3) + rel_cc + 1;
        return ("");
    }

