    function set_val(i, j, k) {
        // <!-- This functions sets value in parameter "k" for row in parameter "i" and column in parameter "j" -->
        if (i > 0 && i < 10 && j > 0 && j < 10)
            tempData[i - 1][j - 1] = k;
    }

    function pos_val(i, j) {
        // <!-- This functions returns the value of the cell in row and column mentioned in the parameters "i" and "j" respectively -->
        if (i > 0 && i < 10 && j > 0 && j < 10)
            return tempData[i - 1][j - 1];
    }

    function solve_problem_itr() {
        // <!-- This functions calls the solve problem function till all the values are determined -->

        tempData = [];
        for (var i = 0; i < data.length; i++) {
            tempData[i] = data[i].slice();
        }

        var m = 0;
        do {
            solve_problem();
            m = m + 1;
        } while (problem_solved != 1)

        // Check for the last cell being empty and fill it
        if (tempData[8][8] === 0) {
            for (var i = 1; i < 10; i++) {
                if (tempData[8].indexOf(i) === -1) {
                    tempData[8][8] = i;
                    break;
                }
            }
        }
        setData(data => { return tempData; });
        setMessage(message => "It took " + dump_val + " iterations to solve the problem");
        return ("");
    }

    function reset_problem() {
        problem_solved = 0;
        dump_val = 0;
        tempData = Array(9).fill().map(() => Array(9).fill(0));
        setData(data => { return tempData; });
        setMessage(message => "");
    }

